<template>
  <div class="plugins">
        <vue-headful
            title="0xsp Mongoose | Plugins"
            description=""
        />         
    <div class="row"> 
      <div class="col-md-4" v-for="plugin in data" :key="plugin.id">
      <card class="plugin">
      <div>
        <a :href="plugin.url">
        <div class="author text-center">
          <img v-if="img" width="128px" class="border-white" :src=" plugin.img" alt="..." />
          <font-awesome-icon v-if="icon" size="6x" transform="shrink-6 left-4" :icon="['fas', plugin.icon]"  />
          <h4 class="title">{{ plugin.title }} </h4>
        </div>
        <div class="text-left">
          <hr>
          <code>
              {{ plugin.description }}
          </code>
          <hr>
          <div class="text-right text-dark">
             Credits: <i>{{ plugin.credit }}</i>
          </div>
        </div>
        </a>
      </div>
    </card>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      data:[],
      noConnection:false,
      img:false,
      icon:true,
    }
  },
  methods : {
    getPlugins(){
        this.axios.get('/mydashboard/getPlugins').then(response => {
          this.data = response.data.plugins.plugins
        });
    },
    checkInternet(){
      this.axios.get('/mydashboard/checkInternetCon').then(res => {
        if(res.data.success){
          this.img = true;
          this.icon = false;
        }else{
          this.img = false;
          this.icon = true;
        }
      });
    },
    checkPlugins(){
        for (var prop in this.data) {
          this.axios.get(`/mydashboard/checkPlugin/${this.data[prop].plugin_file}`).then(response => {
            if(!response.data.success){
              this.axios.post('/mydashboard/installPlugin/',this.data[prop]).then(res => {
                if(!res.data.success){
                  this.noConnection = true
                }
              });
            }
          });
      }
    }
  },
  created(){
    this.checkInternet();
    this.getPlugins();
    setTimeout(() => {
        this.checkPlugins();
    }, 2000);
}
}
</script>

<style>
  .fas{
    font-size: 30px;
  }
</style>